import React, { useEffect, useMemo, useState } from "react";
import "./Greeting.css";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import { greeting } from "../../portfolio";
import { Fade } from "react-reveal";

const loadProfilePhoto = (fileName) => {
  try {
    return require(`../../assets/images/${fileName}`);
  } catch (error) {
    return null;
  }
};

export default function Greeting(props) {
  const theme = props.theme;
  const profilePhotos = useMemo(
    () =>
      [
        "profile_photo.jpg",
        "profile_photo.png",
        "profile2.JPG",
        "profile3.JPG",
        "profile4.JPG",
      ]
        .map(loadProfilePhoto)
        .filter(Boolean),
    []
  );
  const [activePhotoIndex, setActivePhotoIndex] = useState(0);

  useEffect(() => {
    if (profilePhotos.length <= 1) return;
    const intervalId = setInterval(() => {
      setActivePhotoIndex((index) => (index + 1) % profilePhotos.length);
    }, 4500);
    return () => clearInterval(intervalId);
  }, [profilePhotos.length]);

  return (
    <Fade bottom duration={2000} distance="40px">
      <div className="greet-main" id="greeting">
        <div className="greeting-main">
          <div className="greeting-text-div">
            <div>
              <h1 className="greeting-text" style={{ color: theme.text }}>
                {greeting.title}
              </h1>
              {greeting.nickname && (
                <h2 className="greeting-nickname" style={{ color: theme.text }}>
                  ( {greeting.nickname} )
                </h2>
              )}
              <p
                className="greeting-text-p subTitle"
                style={{ color: theme.secondaryText }}
              >
                {greeting.subTitle}
              </p>
              <SocialMedia theme={theme} />
              {/* <div className="portfolio-repo-btn-div">
                <Button
                  text=" Star Me On Github"
                  newTab={true}
                  href={greeting.portfolio_repository}
                  theme={theme}
                  className="portfolio-repo-btn"
                />
              </div> */}
              {/* <div className="button-greeting-div">
              <Button text="Contact me" href="#contact" />
              <Button text="See my resume" newTab={true} href={greeting.resumeLink} />
            </div> */}
            </div>
          </div>
          <div className="greeting-image-div">
            {profilePhotos.length ? (
              <div className="profile-slideshow">
                {profilePhotos.map((photo, index) => (
                  <img
                    key={`profile-${index}`}
                    alt={`Soham Lokhande ${index + 1}`}
                    src={photo}
                    className={`profile-photo-slide ${
                      index === activePhotoIndex ? "is-active" : ""
                    }`}
                  />
                ))}
              </div>
            ) : (
              <div
                style={{
                  width: "100%",
                  height: "400px",
                  backgroundColor: theme.body,
                  borderRadius: "10px",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  color: theme.text,
                }}
              >
                Add your photo as profile_photo.jpg or profile_photo.png
              </div>
            )}
          </div>
        </div>
      </div>
    </Fade>
  );
}
