import React, { Component } from "react";
import "./ExperienceCard.css";
import { Fade } from "react-reveal";

// Create a context for all images in the assets/images directory
// Exclude large files like gifs - only load png, jpg, jpeg, svg
const imageContext = require.context(
  "../../assets/images",
  false,
  /\.(png|jpg|jpeg|svg)$/
);

// Cache the image keys to avoid scanning on every render
const cachedImageKeys = imageContext.keys();

class ExperienceCard extends Component {
  getImageSrc = (logoPath) => {
    if (!logoPath) return null;
    try {
      // Normalize the logo path to match context format (./filename.ext)
      const normalizedPath = `./${logoPath}`;

      // Check if the image exists in the cached keys
      const imageKey = cachedImageKeys.find(
        (key) => key === normalizedPath || key.endsWith(`/${logoPath}`)
      );

      if (imageKey) {
        return imageContext(imageKey);
      }
      return null;
    } catch (error) {
      // Return null if image doesn't exist
      return null;
    }
  };

  splitDescriptionIntoBullets = (description) => {
    if (!description) return [];
    // Split by periods followed by space or end of string, but keep the period
    const sentences = description
      .split(/(?<=\.)\s+/)
      .filter((s) => s.trim().length > 0);
    return sentences;
  };

  render() {
    const experience = this.props.experience;
    const index = this.props.index;
    const totalCards = this.props.totalCards;
    const theme = this.props.theme;
    const logoSrc = this.getImageSrc(experience["logo_path"]);

    // Alternate sides: even index (0, 2, 4...) = right, odd index (1, 3, 5...) = left
    const isRightSide = index % 2 === 0;

    return (
      <div
        className={`experience-list-item ${
          isRightSide ? "experience-right" : "experience-left"
        }`}
        style={{ marginTop: index === 0 ? 30 : 50 }}
      >
        <div className="experience-card-stepper">
          <div
            className="experience-timeline-dot"
            style={{
              width: 20,
              height: 20,
              backgroundColor: `${theme.headerColor}`,
              borderRadius: 50,
              zIndex: 100,
            }}
          />
          {index !== totalCards - 1 && (
            <div
              className="experience-timeline-line"
              style={{
                width: 2,
                backgroundColor: `${theme.headerColor}`,
                position: "absolute",
                top: 20,
                bottom: -50,
              }}
            />
          )}
        </div>

        {isRightSide ? (
          <>
            {/* Right side card */}
            <div className="experience-card-container experience-card-right">
              {logoSrc && (
                <Fade right duration={2000} distance="40px">
                  <div className="experience-card-logo-div">
                    <img
                      className="experience-card-logo"
                      src={logoSrc}
                      alt=""
                    />
                  </div>
                </Fade>
              )}
              <Fade right duration={2000} distance="40px">
                <div className="experience-card-shell">
                  <div
                    className="arrow-left"
                    style={{ borderRight: `10px solid ${theme.body}` }}
                  ></div>
                  <div
                    className="experience-card"
                    style={{ background: `${theme.body}` }}
                  >
                    <div
                      className="experience-card-header-div"
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "space-between",
                      }}
                    >
                      <div>
                        <h3
                          className="experience-card-title"
                          style={{ color: theme.text }}
                        >
                          {experience["title"]}
                        </h3>
                        <p
                          className="experience-card-company"
                          style={{ color: theme.text }}
                        >
                          <a
                            href={experience["company_url"]}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            {experience["company"]}
                          </a>
                        </p>
                      </div>
                      <div>
                        <div className="experience-card-heading-right">
                          <p
                            className="experience-card-duration"
                            style={{ color: theme.secondaryText }}
                          >
                            {experience["duration"]}
                          </p>
                          <p
                            className="experience-card-location"
                            style={{ color: theme.secondaryText }}
                          >
                            {experience["location"]}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        marginTop: 20,
                      }}
                    >
                      <ul
                        className="experience-description-list"
                        style={{
                          margin: 0,
                          paddingLeft: 20,
                          color: theme.text,
                          fontFamily: "Google Sans Regular",
                        }}
                      >
                        {this.splitDescriptionIntoBullets(
                          experience["description"]
                        ).map((sentence, idx) => (
                          <li
                            key={idx}
                            style={{ marginBottom: 8, lineHeight: 1.6 }}
                          >
                            {sentence.trim()}
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </Fade>
            </div>
          </>
        ) : (
          <>
            {/* Left side card */}
            <div className="experience-card-container experience-card-left">
              <Fade left duration={2000} distance="40px">
                <div className="experience-card-shell">
                  <div
                    className="experience-card"
                    style={{ background: `${theme.body}` }}
                  >
                    <div
                      className="experience-card-header-div"
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "space-between",
                      }}
                    >
                      <div>
                        <h3
                          className="experience-card-title"
                          style={{ color: theme.text }}
                        >
                          {experience["title"]}
                        </h3>
                        <p
                          className="experience-card-company"
                          style={{ color: theme.text }}
                        >
                          <a
                            href={experience["company_url"]}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            {experience["company"]}
                          </a>
                        </p>
                      </div>
                      <div>
                        <div className="experience-card-heading-right">
                          <p
                            className="experience-card-duration"
                            style={{ color: theme.secondaryText }}
                          >
                            {experience["duration"]}
                          </p>
                          <p
                            className="experience-card-location"
                            style={{ color: theme.secondaryText }}
                          >
                            {experience["location"]}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "flex-start",
                        marginTop: 20,
                      }}
                    >
                      <ul
                        className="experience-description-list"
                        style={{
                          margin: 0,
                          paddingLeft: 20,
                          color: theme.text,
                          fontFamily: "Google Sans Regular",
                        }}
                      >
                        {this.splitDescriptionIntoBullets(
                          experience["description"]
                        ).map((sentence, idx) => (
                          <li
                            key={idx}
                            style={{ marginBottom: 8, lineHeight: 1.6 }}
                          >
                            {sentence.trim()}
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                  <div
                    className="arrow-right"
                    style={{ borderLeft: `10px solid ${theme.body}` }}
                  ></div>
                </div>
              </Fade>
              {logoSrc && (
                <Fade left duration={2000} distance="40px">
                  <div className="experience-card-logo-div">
                    <img
                      className="experience-card-logo"
                      src={logoSrc}
                      alt=""
                    />
                  </div>
                </Fade>
              )}
            </div>
          </>
        )}
      </div>
    );
  }
}

export default ExperienceCard;
