import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import GithubRepoCard from "../../components/githubRepoCard/GithubRepoCard";
import PublicationCard from "../../components/publicationsCard/PublicationCard";
import Button from "../../components/button/Button";
import ProjectLanguages from "../../components/projectLanguages/ProjectLanguages";
import TopButton from "../../components/topButton/TopButton";
import { Fade } from "react-reveal";
import {
  greeting,
  projectsHeader,
  projects,
  publicationsHeader,
  publications,
} from "../../portfolio.js";
import "./Projects.css";
import ProjectsImg from "./ProjectsImg";

const mediaContext = require.context(
  "../../assets",
  true,
  /\.(png|jpg|jpeg|gif|mp4|webm|mov)$/
);
const cachedMediaKeys = mediaContext.keys();

const getMediaSrc = (mediaPath) => {
  if (!mediaPath) return null;
  if (/^https?:\/\//i.test(mediaPath)) return mediaPath;
  const cleanedPath = mediaPath.replace(/^\.?\//, "").replace(/^assets\//, "");
  const normalizedPath = `./${cleanedPath}`;
  const mediaKey = cachedMediaKeys.find(
    (key) => key === normalizedPath || key.endsWith(`/${cleanedPath}`)
  );
  return mediaKey ? mediaContext(mediaKey) : null;
};

class Projects extends Component {
  state = {
    activeProject: null,
    activeMediaIndex: 0,
  };

  openProjectModal = (project) => {
    this.setState({ activeProject: project, activeMediaIndex: 0 });
  };

  closeProjectModal = () => {
    this.setState({ activeProject: null, activeMediaIndex: 0 });
  };

  goToMediaIndex = (index) => {
    this.setState({ activeMediaIndex: index });
  };

  getProjectMedia = (project) => {
    if (!project) return [];
    const videos = []
      .concat(project.video_paths || [])
      .concat(project.video_path ? [project.video_path] : []);
    const images = []
      .concat(project.image_paths || [])
      .concat(project.image_path ? [project.image_path] : []);
    const media = [];

    videos.forEach((path) => {
      if (path) {
        media.push({ type: "video", src: getMediaSrc(path) });
      }
    });
    images.forEach((path) => {
      if (path) {
        media.push({ type: "image", src: getMediaSrc(path) });
      }
    });

    return media.filter((item) => item.src);
  };

  render() {
    const theme = this.props.theme;
    const allProjects = projects.data;
    const { activeProject, activeMediaIndex } = this.state;
    const activeMedia = this.getProjectMedia(activeProject);
    const detailText =
      activeProject && (activeProject.details || activeProject.description);
    const detailParagraphs = detailText
      ? detailText.split(/\n\s*\n/).filter((paragraph) => paragraph.trim())
      : [];

    return (
      <div className="projects-main">
        <Header theme={theme} />
        <div className="basic-projects">
          <Fade bottom duration={2000} distance="40px">
            <div className="projects-heading-div">
              <div className="projects-heading-img-div">
                {/* <img
											src={require(`../../assets/images/${projectsHeader["avatar_image_path"]}`)}
											alt=""
										/> */}
                <ProjectsImg theme={theme} />
              </div>
              <div className="projects-heading-text-div">
                <h1
                  className="projects-heading-text"
                  style={{ color: theme.text }}
                >
                  {projectsHeader.title}
                </h1>
                <p
                  className="projects-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {projectsHeader["description"]}
                </p>
              </div>
            </div>
          </Fade>
        </div>
        <div className="repo-cards-div-main">
          {/* Render all merged projects */}
          {allProjects.map((repo, index) => {
            return (
              <GithubRepoCard
                key={repo.id || `project-${index}`}
                repo={repo}
                theme={theme}
                onOpen={this.openProjectModal}
              />
            );
          })}
        </div>
        <Button
          text={"More Projects"}
          className="project-button"
          href={greeting.githubProfile}
          newTab={true}
          theme={theme}
        />

        {/* Publications  */}
        {publications.data.length > 0 ? (
          <div className="basic-projects">
            <Fade bottom duration={2000} distance="40px">
              <div className="publications-heading-div">
                <div className="publications-heading-text-div">
                  <h1
                    className="publications-heading-text"
                    style={{ color: theme.text }}
                  >
                    {publicationsHeader.title}
                  </h1>
                  <p
                    className="projects-header-detail-text subTitle"
                    style={{ color: theme.secondaryText }}
                  >
                    {publicationsHeader["description"]}
                  </p>
                </div>
              </div>
            </Fade>
          </div>
        ) : null}

        <div className="repo-cards-div-main">
          {publications.data.map((pub) => {
            return <PublicationCard pub={pub} theme={theme} />;
          })}
        </div>

        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />

        {activeProject && (
          <div
            className="project-modal-overlay"
            onClick={this.closeProjectModal}
          >
            <div
              className="project-modal"
              onClick={(event) => event.stopPropagation()}
              style={{ backgroundColor: theme.highlight }}
            >
              <button
                className="project-modal-close"
                onClick={this.closeProjectModal}
                aria-label="Close project details"
              >
                &times;
              </button>
              <div className="project-modal-media">
                {activeMedia.length > 0 ? (
                  <div className="project-modal-carousel">
                    <button
                      className="project-modal-nav"
                      onClick={() =>
                        this.goToMediaIndex(
                          (activeMediaIndex - 1 + activeMedia.length) %
                            activeMedia.length
                        )
                      }
                      aria-label="Previous media"
                    >
                      &#8592;
                    </button>
                    <div className="project-modal-media-frame">
                      {activeMedia[activeMediaIndex].type === "video" ? (
                        <video
                          src={activeMedia[activeMediaIndex].src}
                          className="project-modal-media-item"
                          controls
                          autoPlay
                          muted
                          playsInline
                        />
                      ) : (
                        <img
                          src={activeMedia[activeMediaIndex].src}
                          alt={activeProject.name}
                          className="project-modal-media-item"
                        />
                      )}
                    </div>
                    <button
                      className="project-modal-nav"
                      onClick={() =>
                        this.goToMediaIndex(
                          (activeMediaIndex + 1) % activeMedia.length
                        )
                      }
                      aria-label="Next media"
                    >
                      &#8594;
                    </button>
                    <div className="project-modal-dots">
                      {activeMedia.map((_, index) => (
                        <button
                          key={`media-dot-${index}`}
                          className={
                            index === activeMediaIndex
                              ? "project-modal-dot active"
                              : "project-modal-dot"
                          }
                          onClick={() => this.goToMediaIndex(index)}
                          aria-label={`Go to media ${index + 1}`}
                        />
                      ))}
                    </div>
                  </div>
                ) : (
                  <div className="project-modal-placeholder">
                    No media available
                  </div>
                )}
              </div>
              <div className="project-modal-info">
                <h2 style={{ color: theme.text }}>{activeProject.name}</h2>
                <div className="project-modal-details">
                  {detailParagraphs.map((paragraph, index) => (
                    <p
                      key={`project-detail-${index}`}
                      className="project-modal-text"
                      style={{ color: theme.text }}
                    >
                      {paragraph.trim()}
                    </p>
                  ))}
                </div>
                {activeProject.languages &&
                  activeProject.languages.length > 0 && (
                    <ProjectLanguages logos={activeProject.languages} />
                  )}
                {activeProject.url && (
                  <Button
                    text={"Visit Project"}
                    className="project-modal-button"
                    href={activeProject.url}
                    newTab={true}
                    theme={theme}
                  />
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default Projects;
