import { createGlobalStyle } from "styled-components";

export const GlobalStyles = createGlobalStyle`
  *,
  *::after,
  *::before {
    box-sizing: border-box;
  }

  body {
    background-color: ${({ theme }) => theme.body};
    background-image: ${({ theme }) =>
      theme.bodyGradient ||
      `linear-gradient(135deg, ${theme.body} 0%, ${theme.highlight} 100%)`};
    color-scheme: dark;
    color: ${({ theme }) => theme.text};
    display: block;
    min-height: 100vh;
    --accent: ${({ theme }) => theme.accent || theme.imageHighlight};
    --accent-glow: ${({ theme }) =>
      theme.accentGlow || "rgba(40, 240, 161, 0.25)"};
    --panel: ${({ theme }) => theme.highlight};
    --panel-border: ${({ theme }) =>
      theme.panelBorder || "rgba(40, 240, 161, 0.12)"};
    --panel-shadow: ${({ theme }) =>
      theme.panelShadow || "0 14px 30px rgba(0, 0, 0, 0.35)"};
    --grid-color: ${({ theme }) =>
      theme.gridColor || "rgba(80, 140, 180, 0.08)"};
    --glow-color: ${({ theme }) =>
      theme.glowColor || "rgba(40, 240, 161, 0.18)"};
    --text-muted: ${({ theme }) => theme.secondaryText};
    // flex-direction: column;
    // justify-content: center;
    // height: 100vh;
    // margin: 0;
    // padding: 0;
    font-family: "Space Grotesk", "IBM Plex Sans", "Montserrat", sans-serif;
    transition: all 0.25s linear;
  }

  body::before,
  body::after {
    content: "";
    position: fixed;
    inset: 0;
    pointer-events: none;
    z-index: 0;
  }

  body::before {
    background-image:
      linear-gradient(var(--grid-color) 1px, transparent 1px),
      linear-gradient(90deg, var(--grid-color) 1px, transparent 1px);
    background-size: 32px 32px;
    opacity: 0.35;
  }

  body::after {
    background: radial-gradient(600px 280px at 70% 10%, var(--glow-color), transparent 70%);
    opacity: 0.6;
  }

  #root {
    position: relative;
    z-index: 1;
    width: 100%;
  }
`;
