import React, { Component } from "react";
import { Route, Switch, BrowserRouter, withRouter } from "react-router-dom";
import Home from "../pages/home/HomeComponent";
import Splash from "../pages/splash/Splash";
import Education from "../pages/education/EducationComponent";
import Experience from "../pages/experience/Experience";
// import Opensource from "../pages/opensource/Opensource";
import Contact from "../pages/contact/ContactComponent";
import Projects from "../pages/projects/Projects";
import { settings } from "../portfolio.js";
import Error404 from "../pages/errors/error404/Error";
import ResumePage from "../pages/resume/Resume.js";

class MainRoutes extends Component {
  componentDidUpdate(prevProps) {
    // Clean up any lingering tooltips or overlays on route change
    if (
      prevProps.location &&
      this.props.location &&
      prevProps.location.pathname !== this.props.location.pathname
    ) {
      // Remove any lingering tooltip elements
      const tooltips = document.querySelectorAll('.tooltip, [role="tooltip"]');
      tooltips.forEach((tooltip) => {
        if (tooltip.parentNode) {
          tooltip.parentNode.removeChild(tooltip);
        }
      });

      // Remove any backdrop overlays
      const backdrops = document.querySelectorAll(
        ".tooltip-backdrop, .modal-backdrop"
      );
      backdrops.forEach((backdrop) => {
        if (backdrop.parentNode) {
          backdrop.parentNode.removeChild(backdrop);
        }
      });

      // Ensure body doesn't have pointer-events disabled
      document.body.style.pointerEvents = "";
      document.body.style.overflow = "";
    }
  }

  render() {
    return (
      <Switch>
        <Route
          path="/"
          exact
          render={(props) =>
            settings.isSplash ? (
              <Splash {...props} theme={this.props.theme} />
            ) : (
              <Home {...props} theme={this.props.theme} />
            )
          }
        />
        <Route
          path="/home"
          render={(props) => <Home {...props} theme={this.props.theme} />}
        />
        <Route
          path="/experience"
          exact
          render={(props) => <Experience {...props} theme={this.props.theme} />}
        />
        <Route
          path="/education"
          render={(props) => <Education {...props} theme={this.props.theme} />}
        />
        {/* <Route
            path="/opensource"
            render={(props) => (
              <Opensource {...props} theme={this.props.theme} />
            )}
          /> */}
        <Route
          path="/contact"
          render={(props) => <Contact {...props} theme={this.props.theme} />}
        />

        {settings.isSplash && (
          <Route
            path="/splash"
            render={(props) => <Splash {...props} theme={this.props.theme} />}
          />
        )}

        <Route
          path="/projects"
          render={(props) => <Projects {...props} theme={this.props.theme} />}
        />
        <Route
          path="/resume"
          render={(props) => <ResumePage {...props} theme={this.props.theme} />}
        />
        <Route
          path="*"
          render={(props) => <Error404 {...props} theme={this.props.theme} />}
        />
      </Switch>
    );
  }
}

const MainWithRouter = withRouter(MainRoutes);

export default class Main extends Component {
  render() {
    return (
      <BrowserRouter basename="/">
        <MainWithRouter theme={this.props.theme} />
      </BrowserRouter>
    );
  }
}
