        "TakeBridge is a B2B SaaS agentic automation platform built to execute real operational workflows across multiple applicationsnot just through APIs, but also through computer-use when a system doesnt expose reliable integrations. The product combines MCP tool adapters (for structured tool calls) with desktop/web automation so teams can automate end-to-end work like data entry, form submission, document handling, and cross-system updates with human oversight when needed.\n\nI led the core engineering across the orchestration + execution stack. On the backend, I built an async FastAPI service layer that exposes production REST APIs and streams live workflow progress to the client via SSE, enabling a real-time operator experience. On the frontend, I built an Electron desktop app (Vite + React) that lets users run workflows, monitor steps, and intervene when required. To make runs reliable and recoverable, I engineered a control plane that provisions isolated, user-scoped AWS EC2 execution sandboxes, persists workflow state in PostgreSQL, and stores run artifacts (outputs, logs, and files) in Cloudflare R2so workflows can be resumed and audited instead of restarted. I also implemented shared multi-agent context/state management using LangChain and moved inference to a self-hosted vLLM deployment to reduce LLM/API costs by ~70%, while shipping continuously through GitHub Actions CI/CD.",
