      details:
        "ASRA (Automated Startup Research Agent) is a LangGraph-powered multi-agent deep research system I built to help founders quickly validate startup ideas with evidence instead of guesses. Given a raw idea, ASRA decomposes it into research dimensions (problem, market, competitors, pricing, go-to-market), generates targeted search queries, and iteratively gathers information from the weboften pulling 50+ sources per runbefore synthesizing the findings into a structured, citation-backed report displayed in a clean UI.\n\nUnder the hood, I engineered a retrieval and context pipeline that keeps outputs high-signal: webpages are chunked and semantically filtered using OpenAI embeddings with ChromaDB top-k retrieval and a relevance gate, so only the most relevant passages enter the reasoning loop. To prevent context bloat, ASRA compresses evidence into deduplicated memory notes and retrieves only the top notes per dimension during synthesis, cutting token usage significantly while maintaining coverage. The system runs behind a FastAPI service with async job execution, and the Next.js frontend streams progress and renders saved reports from a Postgres-backed datastore.",

