        "As a Research Assistant at the Donald Bren School of ICS, I worked on deploying EdgeLake, an open-source permissioned decentralized database, to transform edge devices into a unified, queryable data network. I led the deployment of EdgeLake master, operator, and query nodes across a hybrid setup consisting of Raspberry Pi devices and a host machine, enabling distributed data ingestion, storage, and querying without moving data off-device. This architecture replaced a conventional centralized backend with edge-native SQLite/PostgreSQL databases, allowing the system to behave as a single logical database despite being physically distributed.\n\nI applied this system to a bioanalytic organoid detection pipeline, where edge devices captured experimental images and metadata that needed to be queried across experiments in real time. By decentralizing storage and pushing computation closer to the data source, the system achieved a 40% reduction in query latency while improving fault tolerance and scalability. I also implemented a metadata-driven querying workflow and contributed to an early ML-assisted analysis pipeline that retrieved relevant historical experiment data for cross-experiment comparison and visualization. This work gave me hands-on experience with distributed systems, edge computing, and context-preserving data retrieval, particularly around the challenge of maintaining long-horizon experimental context without overwhelming downstream LLMs.",
